export class JobApplicantEmploymentViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#job_applicant_employment_view_container',

			country_array: null,
			province_array: null,
			e_province_array: null,
			i_province_array: null,
			employment_status_array: null,
			wage_type_array: null,

			company_api: null,

			sub_view_grid_autosize: true,
			sub_view_mode: true
		} );

		super( options );
	}

	initOptions() {
		var options = [
			{ option_name: 'employment_status', api: this.api },
			{ option_name: 'wage_type', api: this.api },
			{ option_name: 'country', field_name: 'country', api: this.company_api },
		];

		this.initDropDownOptions( options );
	}

	showNoResultCover( show_new_btn ) {
		super.showNoResultCover( ( this.sub_view_mode ) ? true : false );
	}

	onAddClick() {
		if ( this.sub_view_mode ) {
			this.buildContextMenu( true );
		}

		super.onAddClick();
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'JobApplicantEmploymentEditView.html';
		this.permission_id = 'job_applicant';
		this.viewId = 'JobApplicantEmployment';
		this.script_name = 'JobApplicantEmploymentView';
		this.table_name_key = 'job_applicant_employment';
		this.context_menu_name = $.i18n._( 'Employment' );
		this.navigation_label = $.i18n._( 'Job Applicant Employment' );
		this.api = TTAPI.APIJobApplicantEmployment;
		this.company_api = TTAPI.APICompany;
		this.render();

		//call init data in parent view
		if ( !this.sub_view_mode ) {
			this.initData();
		}
	}

	buildEditViewUI() {
		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_job_applicant_employment': { 'label': $.i18n._( 'Job Applicant Employment' ), 'is_multi_column': true },
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		this.navigation.AComboBox( {
			api_class: TTAPI.APIJobApplicantEmployment,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_job_applicant_employment',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		var tab_job_applicant_employment = this.edit_view_tab.find( '#tab_job_applicant_employment' );
		var tab_job_applicant_employment_column1 = tab_job_applicant_employment.find( '.first-column' );
		var tab_job_applicant_employment_column2 = tab_job_applicant_employment.find( '.second-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_job_applicant_employment_column1 );
		this.edit_view_tabs[0].push( tab_job_applicant_employment_column2 );

		var form_item_input;
		var widgetContainer;
		var label;

		// tab_job_applicant_employment column1

		// Company Name
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'company_name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Company Name' ), form_item_input, tab_job_applicant_employment_column1, '' );

		form_item_input.parent().width( '45%' );

		// Job Title
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'title', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Title' ), form_item_input, tab_job_applicant_employment_column1 );

		// Description
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_AREA );

		form_item_input.TTextArea( { field: 'description' } );

		this.addEditFieldToColumn( $.i18n._( 'Description' ), form_item_input, tab_job_applicant_employment_column1, '', null, null, true );

		// Address (Line 1)

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'address1', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Address (Line 1)' ), form_item_input, tab_job_applicant_employment_column1 );

		form_item_input.parent().width( '45%' );

		// Address (Line 2)

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'address2', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Address (Line 2)' ), form_item_input, tab_job_applicant_employment_column1 );

		form_item_input.parent().width( '45%' );

		// City
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'city', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'City' ), form_item_input, tab_job_applicant_employment_column1 );

		// Country
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'country', set_empty: true } );
		form_item_input.setSourceData( $this.country_array );
		this.addEditFieldToColumn( $.i18n._( 'Country' ), form_item_input, tab_job_applicant_employment_column1 );

		// Province/State
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'province' } );
		form_item_input.setSourceData( [] );
		this.addEditFieldToColumn( $.i18n._( 'Province/State' ), form_item_input, tab_job_applicant_employment_column1 );

		// Postal / ZIP Code
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'postal_code', width: 90 } );
		this.addEditFieldToColumn( $.i18n._( 'Postal/ZIP Code' ), form_item_input, tab_job_applicant_employment_column1 );

		// Employment Status
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'employment_status_id' } );
		form_item_input.setSourceData( $this.employment_status_array );
		this.addEditFieldToColumn( $.i18n._( 'Employment Status' ), form_item_input, tab_job_applicant_employment_column1 );

		// Wage Type
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'wage_type_id' } );
		form_item_input.setSourceData( $this.wage_type_array );
		this.addEditFieldToColumn( $.i18n._( 'Wage Type' ), form_item_input, tab_job_applicant_employment_column1 );

		// Wage
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'wage', width: 50 } );
		this.addEditFieldToColumn( $.i18n._( 'Wage' ), form_item_input, tab_job_applicant_employment_column1 );

		// Start Date
		form_item_input = Global.loadWidgetByName( FormItemType.DATE_PICKER );
		form_item_input.TDatePicker( { field: 'start_date' } );
		this.addEditFieldToColumn( $.i18n._( 'Start Date' ), form_item_input, tab_job_applicant_employment_column1 );

		// End Date
		form_item_input = Global.loadWidgetByName( FormItemType.DATE_PICKER );
		form_item_input.TDatePicker( { field: 'end_date' } );
		this.addEditFieldToColumn( $.i18n._( 'End Date' ), form_item_input, tab_job_applicant_employment_column1 );

		// Current Employer
		form_item_input = Global.loadWidgetByName( FormItemType.CHECKBOX );
		form_item_input.TCheckbox( { field: 'is_current_employer' } );
		this.addEditFieldToColumn( $.i18n._( 'Current Employer' ), form_item_input, tab_job_applicant_employment_column2, '' );

		// Contact First Name
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'contact_first_name', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Contact First Name' ), form_item_input, tab_job_applicant_employment_column2 );

		// Contact Last Name
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'contact_last_name', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Contact Last Name' ), form_item_input, tab_job_applicant_employment_column2 );

		// Contact Title
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'contact_title', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Contact Title' ), form_item_input, tab_job_applicant_employment_column2 );

		// Contact Work Phone
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'contact_work_phone', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Contact Work Phone' ), form_item_input, tab_job_applicant_employment_column2 );

		// Contact Mobile Phone
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'contact_mobile_phone', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Contact Mobile Phone' ), form_item_input, tab_job_applicant_employment_column2 );

		// Contact Work Email
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'contact_work_email', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Contact Work Email' ), form_item_input, tab_job_applicant_employment_column2 );

		// May we contact this person/employer

		form_item_input = Global.loadWidgetByName( FormItemType.CHECKBOX );
		form_item_input.TCheckbox( { field: 'is_contact_available' } );
		this.addEditFieldToColumn( $.i18n._( 'May we contact this person/employer' ), form_item_input, tab_job_applicant_employment_column2 );

		// Reason for leaving
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_AREA );

		form_item_input.TTextArea( { field: 'leave_reason' } );

		this.addEditFieldToColumn( $.i18n._( 'Reason for leaving' ), form_item_input, tab_job_applicant_employment_column2, '', null, null, true );

		//Tags
		form_item_input = Global.loadWidgetByName( FormItemType.TAG_INPUT );

		form_item_input.TTagInput( { field: 'tag', object_type_id: 380 } );
		this.addEditFieldToColumn( $.i18n._( 'Tags' ), form_item_input, tab_job_applicant_employment_column2, '', null, null, true );
	}

	onFormItemChange( target, doNotValidate ) {
		this.setIsChanged( target );
		this.setMassEditingFieldsWhenFormChange( target );
		var key = target.getField();

		this.current_edit_record[key] = target.getValue();

		switch ( key ) {

			case 'country':
				var widget = this.edit_view_ui_dic['province'];
				widget.setValue( null );
				break;

		}

		if ( key === 'country' ) {
			this.onCountryChange();
			return;
		}
		if ( !doNotValidate ) {
			this.validate();
		}
	}

	setCurrentEditRecordData() {

		//Set current edit record data to all widgets
		//Set current edit record data to all widgets
		for ( var key in this.current_edit_record ) {
			var widget = this.edit_view_ui_dic[key];
			if ( Global.isSet( widget ) ) {
				switch ( key ) {
					case 'country': //popular case
						this.setCountryValue( widget, key );
						break;
					default:
						widget.setValue( this.current_edit_record[key] );
						break;
				}

			}
		}
		this.collectUIDataToCurrentEditRecord();
		this.setEditViewDataDone();
	}

	setProvince( val, m ) {
		var $this = this;

		if ( !val || val === '-1' || val === '0' ) {
			$this.province_array = [];
			this.adv_search_field_ui_dic['province'].setSourceData( [] );
		} else {

			this.company_api.getOptions( 'province', val, {
				onResult: function( res ) {
					res = res.getResult();
					if ( !res ) {
						res = [];
					}

					$this.province_array = Global.buildRecordArray( res );
					$this.adv_search_field_ui_dic['province'].setSourceData( $this.province_array );

				}
			} );
		}
	}

	eSetProvince( val, refresh ) {
		var $this = this;
		var province_widget = $this.edit_view_ui_dic['province'];

		if ( !val || val === '-1' || val === '0' ) {
			$this.e_province_array = [];
			province_widget.setSourceData( [] );
		} else {
			this.company_api.getOptions( 'province', val, {
				onResult: function( res ) {
					res = res.getResult();
					if ( !res ) {
						res = [];
					}

					$this.e_province_array = Global.buildRecordArray( res );
					if ( refresh && $this.e_province_array.length > 0 ) {
						$this.current_edit_record.province = $this.e_province_array[0].value;
						province_widget.setValue( $this.current_edit_record.province );
					}
					province_widget.setSourceData( $this.e_province_array );

				}
			} );
		}
	}

	onGridSelectRow() {
		if ( this.sub_view_mode ) {
			this.buildContextMenu( true );
		} else {
			this.buildContextMenu();
		}
		this.setDefaultMenu();
	}

	searchDone() {

		super.searchDone();

		TTPromise.resolve( 'JobApplicant_History_Tab', 'JobApplicantEmploymentViewController' );

	}

}

JobApplicantEmploymentViewController.loadSubView = function( container, beforeViewLoadedFun, afterViewLoadedFun ) {
	Global.loadViewSource( 'JobApplicantEmployment', 'SubJobApplicantEmploymentView.html', function( result ) {
		var args = {};
		var template = _.template( result );

		if ( Global.isSet( beforeViewLoadedFun ) ) {
			beforeViewLoadedFun();
		}
		if ( Global.isSet( container ) ) {
			container.html( template( args ) );
			if ( Global.isSet( afterViewLoadedFun ) ) {
				afterViewLoadedFun( sub_job_applicant_employment_view_controller );
			}
		}
	} );
};